html,
body,
#root,
.App {
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
}

.Header {
  flex: 0 1 auto;
}

.Main {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  overflow-y: scroll;
}

.Main__container {
  display: flex;
  flex: 1;
}

.Puzzle {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.Puzzle form {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 80%;
}

.Puzzle form div {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.monospace {
  font-family: monospace;
}

.font-small {
  font-size: 6px;
}

html body #root .App .PuzzleInput__textarea {
  flex: 1;
  font-family: monospace;
  height: 100%;
}

.Puzzle__back__text {
  vertical-align: middle;
  padding-left: 0.2rem;
}

div.Solution__Jumbotron {
  padding: 2rem 1.5rem;
}
